#root {
  background-image: linear-gradient(to top, #36D1DC, #5B86E5);
  background-repeat: 'no-repeat';
  background-position: 'center';
  min-height: 100vh;
  background-size: cover;
  margin: 0;
  background-size: cover;
}

body,
html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to top, #36D1DC, #5B86E5);
  background-repeat: 'no-repeat';
  background-position: 'center';
  min-height: 100vh;
  background-size: cover;
  margin: 0;
  background-size: cover;
}

.info {
 border: none !important; 
}

p {
  font-weight: bold;
  color: black;
  font-size: 1.5em;
}

li {
  font-weight: bold;
  color: black;
  font-size: 1.5em; 
}
.container {
  border: 1px solid white;
  padding: 10px;
  margin-bottom: 2em;
  margin-top: 30px;
}

.heading {
  color: white;
  text-align: center;
  font-size: 3em;
  margin: 40px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.label {
  text-align: right;
  color: white;
  font-size: 1.5em;
}

.inputfield {
  text-align: left;
}

@media only screen and (max-width: 600px) {
.label {
  text-align: center;
  color: white;
  font-size: 1.5em;
}
.inputfield {
  text-align: center;
}
}

.container {
  border-radius: 5px !important;
  transition: 2s ease-in-out !important;
}

.input {
  max-width: 70px;
  border-radius: 5px;
  background-color: transparent;
  border: 1px solid white;
  color: white;
  padding: 5px;
  margin: 5px;
}

.input::after {
  content: "mm";
  position: relative;
  height: 1em;
  width: 3em;
  color: white;
}

.inputscontainer {
  max-width: 600px;
}

.density {
  color: white;
  margin-bottom: 20px;
  font-size: 1.5em;
}

.buttonfat {
  background-color: transparent;
  border: 1px solid white;
  color: white;
  padding: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 2em;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transform: translateX(-50%);
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
}

.buttonfat:hover {
  background-image: linear-gradient(to top, #FF416C, #FF4B2B);
}

a {
  color: #FF416C !important;
  font-weight: bold;
  transition: 0.5s ease-in-out;
}

a:hover {
  text-decoration: none !important;
  color: rgb(141, 22, 50) !important;
  margin: 0 5px;
}

.bodyfat {
  text-align: center;
  color: white;
  margin-bottom: 20px;
  font-size: 4em;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
}

.instruction {
  text-align: center;
  color: white;
  margin-bottom: 20px;
  font-size: 2em;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
}


@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}


@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

/* @media only screen and (min-width: 600px) {
  .label {
    display: block !important;
  }
} */

@media only screen and (max-width: 600px) {
.instruction {
  font-size: 1.2em;
}

  .bodyfat {
    font-size: 2em;
  }
  body {
height: 100%;
  }
}

@media only screen and (max-width: 370px) {
  .heading {
    font-size: 2em;
  }
}

.infobttn {
 margin: auto !important;
}



.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.dropDownMenu{
  background-color: #5a6268 !important;
  color: white !important;
}
.dropdownItem {
  color: white !important;
  transition: border-radius 0.5s ease-in-out; 
  transition: margin 0.5s ease-in-out;
}


.dropdownItem:hover {
  border-radius: 10px !important;
  margin: 0 10px;
  background-color: white !important;
  color: #5a6268 !important;
}
